/* Wisdom Script */
@font-face {
  font-family: 'wisdom';
  src: url('../font/wisdomscriptaj.woff2') format('woff2'),
  url('../font/wisdomscriptaj.woff') format('woff'),
  url('../font/wisdomscriptaj.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

